import buttomBar from '@/components/bottomBar'
export default {
  name: "wallet",
  data(){
    return{
      startTime:'', //开始时间
      endTime:'', //结束时间
      dataList:[] //记录列表
    }
  },
  computed:{
    // 选择日期区间内的收入总额
    inputMoney:{
      get: function () {
        let sum = 0;
        for(let x of this.dataList){
          if(x.category === '收入'){
            sum += parseFloat(x.money);
          }
        }
        return sum;
      }
    },
    // 选择日期区间内的支出总额
    outputMoney:{
      get: function () {
        let sum = 0;
        for(let x of this.dataList){
          if(x.category === '支出'){
            sum += parseFloat(x.money);
          }
        }
        return sum;
      }
    }
  },
  components:{
    'buttomBar':buttomBar
  },
  methods:{
    // 打开日期选择器
    showDatePicker:function (date,flag) {
      let self = this;
      this.$vux.datetime.show({
        value: date, //初始日期
        onHide () {

        },
        onShow () {

        },
        // 点击确定时
        onConfirm (){
          // 用户选择的开始日期不能大于结束日期
          let startTime = self.startTime;
          let endTime = self.endTime;
          if(flag === 'start'){
            startTime = this.value;
          } else if(flag === 'end'){
            endTime = this.value;
          }
          // 将日期转化为number类型
          let start = parseInt(startTime.split('-').join(''));
          let end = parseInt(endTime.split('-').join(''));
          // 比较两个日期的大小
          if((start - end) > 0){
            self.$alert.on('开始日期不能大于结束日期');
            return;
          } else {
            // 符合条件时更改页面上日期的值
            if(flag === 'start'){
              self.startTime = this.value;
            } else if(flag === 'end'){
              self.endTime = this.value;
            }
            // 开始发查询接口
            self.queryLog();
          }
          console.log(start);
          console.log(end);
        }
      })
    },

    // 检查月份和日期，不足两位前面补0
    dateCheck:function (arr) {
      if(arr[1].length < 2){
        arr[1] = '0' + arr[1];
      }
      if(arr[2].length < 2){
        arr[2] = '0' + arr[2];
      }
    },

    // 查询记录方法
    queryLog:function () {
      let self = this;
      self.$waitting.on();
      self.$cAxios.post(self.$constantIP.queryLog, {
        params:{
          startTime:self.startTime,
          endTime:self.endTime
        }
      })
        .then(function (response) {
          self.$waitting.off();
          if(response.data.returnCode === '000000'){
            self.dataList = response.data.dataList;
          }
        })
    }
  },
  beforeMount:function(){
    // 初始查询时间区间为当前日期往前数30天
    // 获取当前时间
    let endDate = new Date();
    // 往前推30天
    let startDate = new Date(endDate.getTime() - 1000*60*60*24*30);
    // 将时间对象转化成对应日期插件格式的字符串
    startDate = startDate.toLocaleDateString().split('/');
    endDate = endDate.toLocaleDateString().split('/');
    this.dateCheck(startDate);
    this.dateCheck(endDate);
    startDate = startDate.join('-');
    endDate = endDate.join('-');
    this.startTime = startDate;
    this.endTime = endDate;
  },
  mounted:function () {
    // 阻止滑动穿透
    document.querySelector(".topBg").addEventListener("touchmove", (event) => {
      event.preventDefault();  //阻止默认行为
      event.stopPropagation(); //阻止冒泡
    }, false);
    document.querySelector(".category").addEventListener("touchmove", (event) => {
      event.preventDefault();  //阻止默认行为
      event.stopPropagation(); //阻止冒泡
    }, false);

    // 页面初始化时查询近30天的记录
    this.queryLog();
  }
}



