export default {
    name: "login",
    data() {
      return{
        username:"",
        password:"",
        usernameBottom:false,
        passwordBottom:false
      }
    },
    computed:{
      // 判断用户名是否输入
      usernameIcon:function() {
        return (this.username === "" || this.username === undefined || this.username === null);
      },
      // 判断密码是否输入
      passwordIcon:function() {
        return (this.password === "" || this.password === undefined || this.password === null);
      }
    },
    methods:{
      // 清空账号密码输入框的内容
      clearInput:function (value) {
        switch (value){
          case "username":
            this.username = "";
            break;
          case "password":
            this.password = "";
            break;
        }
      },
      // 点击登录
      loginSubmit:function () {
        let self = this;
        if(self.usernameIcon){
          self.$alert.on('请输入用户名');
        } else if(self.passwordIcon){
          self.$alert.on('请输入密码');
        } else {
          self.$waitting.on();
          this.$cAxios.post(self.$constantIP.login, {
            params:{
              username:this.username,
              password:this.$md5(this.password)
            }
          }).then(function (response) {
            console.log(response);
            self.$waitting.off();
            if(response.data.returnCode === '000000'){
              // self.$alert.on('登录成功');
              self.$router.push('/mainPage/wallet');
            }
          })
        }
      },
      // 点击注册
      goRegister:function () {
        this.$router.push('/account/register');
      },
      // input聚焦时
      onFocus:function (value) {
        switch (value) {
          case 'usernameBottom':
            this.usernameBottom = true;
            break;
          case 'passwordBottom':
            this.passwordBottom = true;
            break;
        }

      },
      // input丢失焦点时
      outFocus:function (value) {
        switch (value) {
          case 'usernameBottom':
            this.usernameBottom = false;
            break;
          case 'passwordBottom':
            this.passwordBottom = false;
            break;
        }
      }
    }
  }



